.project-link {
  @include flex(flex-end, normal, column);
  @include cover;
  color: $white;
  height: 480px;
  padding: 20px;
  @include sm {
    padding: 10px; }
  &__title {
    display: block;
    @include bold;
    margin-bottom: 10px; }
  &__subttl {
    color: $white-dark;
    display: block;
    font-size: $fz-sm; } }
