.nav {
  @include os-bold;
  li {
    display: inline-block;
    margin-right: 60px; }
  a {
    &.is-active {
      color: $white; }
    @include hover {
      color: $white; } } }

.nav-hero {
  ul {
    @include flex(flex-start, normal, row, wrap); }
  li {
    margin-bottom: 15px;
    width: 50%;
    @include md {
      margin-bottom: 10px; }
    @include sm {
      margin-bottom: 5px; }
    &::before {
      content: '-';
      display: inline-block;
      // vertical-align: middle
      margin-right: 5px; } }
  a {
    text-decoration: underline;
    @include tr(color .3s);
    @include hover {
      text-decoration: none;
      color: $accent; }
    &.is-active {
      text-decoration: none;
      color: $accent; } } }


