.slider--projects .slick-slide { margin-left: 15px; margin-right: 15px; }

.prev, .next { position: relative; width: 40px; height: 40px; color: #323232; display: inline-flex; justify-content: center; align-items: center; transform: translate(0, -50%); position: absolute; top: 50%; }

.prev::before, .next::before { content: ''; background-color: #ffc600; top: 0; bottom: 0; left: 0; right: 0; transform: skew(15deg); position: absolute; z-index: 0; }

.prev::after, .next::after { content: ''; width: 10px; height: 10px; border-left: 1px solid currentColor; border-bottom: 1px solid currentColor; transform: rotate(45deg); }

.prev { left: -100px; }

.next { right: -100px; }

.next::before { content: ''; transform: skew(-15deg); }

.next::after { content: ''; transform: rotate(-135deg); }

.project-link { display: flex; justify-content: flex-end; align-items: normal; flex-direction: column; flex-wrap: nowrap; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; color: #fff; height: 480px; padding: 20px; }

@media only screen and (max-width: 767px) { .project-link { padding: 10px; } }

.project-link__title { display: block; font-family: 'helveticaneuecyr-bold', Arial, sans-serif; margin-bottom: 10px; }

.project-link__subttl { color: #f2f2f2; display: block; font-size: 14px; }

.item { background-repeat: no-repeat; background-position: 100% 0%; background-size: auto; position: relative; background-color: #fff; padding: 20px; display: block; box-shadow: 0px 2px 17px 0px rgba(0, 0, 0, 0.09); transition: transform 0.3s; }

.no-touch .item:hover { transform: scale(1.1); }

.no-touch .item:hover .item__link { color: #323232; }

.no-touch .item:hover .item__link .link-arrow__arr { width: 80px; }

@media only screen and (max-width: 767px) { .item { padding: 10px; } }

.item__price { display: block; }

.item__content { display: block; margin-top: 70%; }

.item__title { display: block; margin-bottom: 15px; }

.about__content { padding-top: 90px; }

.about-items:after { content: " "; display: table; clear: both; }

.about-item { background-repeat: no-repeat; background-position: 100% 100%; padding-top: 100%; position: relative; }

.about-item__wrap { width: 50%; padding-left: 15px; padding-right: 15px; float: left; }

.about-item__wrap:nth-child(1) { margin-bottom: 30px; }

.about-item__wrap:nth-child(2) { float: right; }

.about-item__wrap:nth-child(2) .about-item { padding-top: calc(200% + 30px); }

.about-item__content { position: absolute; top: 40px; left: 20px; }

.about-item__title { margin-bottom: 15px; }

.about-item__text { max-width: 230px; }

.form__field { margin-bottom: 15px; }

.form__btn { margin-bottom: 15px; }

.form--call { max-width: 330px; }

.form--call .form__field { margin-bottom: 40px; text-align: center; }

.form--call .form__bottom { white-space: nowrap; }

.input { font-family: 'helveticaneuecyr-bold', Arial, sans-serif; }

.input .icon { font-size: 21px; margin-right: 10px; position: relative; top: 0.2em; }

.input input { background-color: transparent; }

.contacts { display: flex; justify-content: flex-start; align-items: normal; flex-direction: row; flex-wrap: wrap; }

.contacts__content { width: 34.78261%; }

.contacts__content .contact:not(:last-child) { margin-bottom: 15px; }

.contacts__social { text-align: center; }

.contacts__btn { text-align: right; margin-top: 25px; width: 100%; }

.contacts__map { position: relative; padding-top: 40%; width: 65.21739%; }

.contacts__map iframe { position: absolute; top: 0; left: 0; width: 100%; height: 100%; }

.contact { font-family: 'Oswaldregularitalic', Arial, sans-serif; color: #323232; position: relative; padding-left: 30px; }

.contact__icon { position: absolute; top: 50%; left: 0; transform: translate(0, -50%); }

.contact__icon .icon { font-size: 25px; }

.contact__content a { text-decoration: underline; }
