.item {
  $this: &;
  background-repeat: no-repeat;
  background-position: 100% 0%;
  background-size: auto;
  position: relative;
  background-color: $white;
  padding: 20px;
  display: block;
  box-shadow: 0px 2px 17px 0px rgba(0, 0, 0, 0.09);
  @include tr(transform .3s);
  @include hover {
    @include scale(1.1);
    #{$this}__link {
      color: $black-light;
      .link-arrow__arr {
        width: 80px; } } }
  @include sm {
    padding: 10px; }
  &__price {
    // position: absolute
    // top: 20px
    // left: 20px
    display: block; }
  &__content {
    display: block;
    margin-top: 70%; }
  &__title {
    display: block;
    margin-bottom: 15px; } }



