.section {
  $this: &;
  position: relative;
  .word {
    left: percentage(3/12);
    top: 0; }
  &__title {
    position: relative;
    margin-bottom: 25px;
    @include md {
      margin-bottom: 15px; } }
  &__top {
    @include flex(space-between,center);
    margin-bottom: 40px;
    @include md {
      margin-bottom: 25px; }
    @include sm {
      margin-bottom: 15px; }
    #{$this}__title {
      margin-bottom: 0; } }
  &__subttl {
    margin-bottom: 35px;
    @include md {
      margin-bottom: 25px; }
    @include sm {
      margin-bottom: 15px; } }
  &__items {
    margin-top: 30px;
    margin-bottom: 30px; }

  &--white {
    color: $white; }
  &--bg {
    @include cover; } }

.s-about {
  background-size: 25%;
  background-position: 50% 100%;
  background-repeat: no-repeat;
  .word {
    @include css-lock(30, 100, 320, 1499, top); } }




