.btn {
  display: inline-block;
  // background-color: $accent
  text-align: center;
  @include bold;
  font-size: 15px;
  color: $black-light;
  min-width: 275px;
  padding: 1em;
  position: relative;
  @include before {
    position: absolute;
    z-index: 0;
    background-color: $accent;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    transform: skew(-15deg); }
  span {
    position: relative;
    z-index: 1; }


  &--full {
    width: 100%;
    min-width: 0; } }
